body {
  background-color: black;
  font-family: "Red Hat Display", sans-serif;
  font-size: 1.2rem;
  overflow-x: hidden;
  margin: 0;
 }

canvas {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
}
/* Navbar styling */

header {
  display: flex;
  justify-content: center;
  box-shadow: 0 0.5rem 0.5rem 0.2rem rgba( 0, 0, 0, 0.8);
  z-index: 101;
  background-color: rgba( 0, 0, 0, 0.8);
  margin-left: 0;
  width: 70vw;
  margin-right: 0;
  position: sticky;
  top: 0;
  left: 0;
  padding-top: 1rem;
  padding-left: 15%;
  padding-right: 15%;
  height: 5%;
}

nav {
  width: 60%;
}

#menu #dropdown-menu {
  visibility: hidden;
  width: 0;
}

#menu, #submenu {
  display: flex;
  flex-direction: row;
  text-align: center;
  width: 100%;
  padding-left: 0;
}

li {
  list-style-type: none;
  width: 33.3%;
  text-align: center;
  line-height: 1rem;
}

a, a:hover, a:visited, a:active{
  position: relative;
  display: inline-block;
  text-decoration: none;
  color: white;
  text-transform: uppercase;
  letter-spacing: 0.28rem;
}

a:hover {
  text-shadow: 0 0 0.2rem white;
}

#submenu li a {
  position: relative;
  display: inline-block;
}

#submenu li a::after {
  content: "";
  position: absolute;
  width: 0%;
  height: 0.1rem;
  display: block;
  background-color: white;
  transition: width 0.2s ease-in-out;
}

#submenu li a:hover::after {
  width: 99%;   
}

/* Main content */

main {
  top: 2vw;
}

main, footer {
  width: 60vw;
  position: relative;
  z-index: 100;
  margin-left: 20%;
  margin-right: 20%;
  color: white;
}

#hero, #about-me, #projects, #connect-with-me {
  margin-top: 5%;
  margin-bottom: 12%;
  background-color: rgba( 0, 0, 0, 0.5);
  border-radius: 1rem;
  padding: 3rem;
}

#hero {
   padding-top: 25%;
   padding-bottom: 25%;
   padding-left: 6%;
}

h1 {
  font-size: 4rem;
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}

h2 {
  font-size: 2.5rem;
}

#about-me, #projects, #connect-with-me {
  text-align: center;
}

footer p {
  margin-top: 10%;
  margin-bottom: 2rem;
  font-size: 0.8rem;
  text-align: center;
}

#connect-with-me ul {
  display: flex;
  flex-direction: row;
}

#read-more-btn {
  visibility: hidden;
  height: 0;
}

@media (max-width: 1750px) {
  header {
    padding-top: 0;
  }

  nav {
    width: 100%;
  }

  #menu {
    flex-direction: column;
  }

  #menu #dropdown-menu {
    text-align: center;
    visibility: visible;
  }

  ul#menu {
    position: relative;
    cursor: pointer;
  }
  
  #menu #submenu{
    display: flex;
    width: 100%;
    text-align: center ;
    flex-direction: column;
    opacity: 0;
    pointer-events: none;
    position: absolute;
    top: 100%;
    left: 0;
    background-color: black;
    border-radius: 1rem;
    transition: all 0.3s ease;
  }
  
  #menu #submenu li {
    color: white;
    padding: 1rem;
    border-radius: 1rem;
    cursor: pointer;
    transition: background 0.2s ease;
    align-self: center;
    width: 100%;
  }
  
  #menu #dropdown-menu {
    padding: 0.2rem 48%;
    width: 1.2rem;
    transition: transform 0.2s ease;
  }
  
  /* #menu #dropdown-menu:hover {
    transform: rotate(180deg);
  } */
  
  #menu:hover #submenu {
    top: 100%;
    opacity: 1;
    pointer-events: all;
  }

  /* Main Content */
  p {
    font-size: 1rem;
  }

  h1 {
    font-size: 3rem;
  }
  
  main {
    width: 90%;
    margin-right: 5%;
    margin-left: 5%;
  }

  #hero {
    padding: 6.5rem 2rem;
  }

  #about-me, #projects, #connect-with-me {
    padding: 1rem;
  }

  .about-me-hidden {
    display: none;

    transition: all 1s ease-in-out;
  }

  .about-me-hidden.show {
    display: block;
    height: auto;
    height: calc-size(auto);
    
  }

  #read-more-btn {
    font-size: 1rem;
    visibility: visible;
    background-color: transparent;
    color: white;
    border-radius: 50%;
    width: 2.5rem;
    height: 2.5rem;
    border: none;
  }


}