body {
  background-color: #000;
  margin: 0;
  font-family: Red Hat Display, sans-serif;
  font-size: 1.2rem;
  overflow-x: hidden;
}

canvas {
  z-index: 1;
  position: fixed;
  top: 0;
  left: 0;
}

header {
  z-index: 101;
  background-color: #000c;
  justify-content: center;
  width: 70vw;
  height: 5%;
  margin-left: 0;
  margin-right: 0;
  padding-top: 1rem;
  padding-left: 15%;
  padding-right: 15%;
  display: flex;
  position: sticky;
  top: 0;
  left: 0;
  box-shadow: 0 .5rem .5rem .2rem #000c;
}

nav {
  width: 60%;
}

#menu #dropdown-menu {
  visibility: hidden;
  width: 0;
}

#menu, #submenu {
  text-align: center;
  flex-direction: row;
  width: 100%;
  padding-left: 0;
  display: flex;
}

li {
  text-align: center;
  width: 33.3%;
  line-height: 1rem;
  list-style-type: none;
}

a, a:hover, a:visited, a:active {
  color: #fff;
  text-transform: uppercase;
  letter-spacing: .28rem;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

a:hover {
  text-shadow: 0 0 .2rem #fff;
}

#submenu li a {
  display: inline-block;
  position: relative;
}

#submenu li a:after {
  content: "";
  background-color: #fff;
  width: 0%;
  height: .1rem;
  transition: width .2s ease-in-out;
  display: block;
  position: absolute;
}

#submenu li a:hover:after {
  width: 99%;
}

main {
  top: 2vw;
}

main, footer {
  z-index: 100;
  color: #fff;
  width: 60vw;
  margin-left: 20%;
  margin-right: 20%;
  position: relative;
}

#hero, #about-me, #projects, #connect-with-me {
  background-color: #00000080;
  border-radius: 1rem;
  margin-top: 5%;
  margin-bottom: 12%;
  padding: 3rem;
}

#hero {
  padding-top: 25%;
  padding-bottom: 25%;
  padding-left: 6%;
}

h1 {
  margin-top: .5rem;
  margin-bottom: .5rem;
  font-size: 4rem;
}

h2 {
  font-size: 2.5rem;
}

#about-me, #projects, #connect-with-me {
  text-align: center;
}

footer p {
  text-align: center;
  margin-top: 10%;
  margin-bottom: 2rem;
  font-size: .8rem;
}

#connect-with-me ul {
  flex-direction: row;
  display: flex;
}

#read-more-btn {
  visibility: hidden;
  height: 0;
}

@media (width <= 1750px) {
  header {
    padding-top: 0;
  }

  nav {
    width: 100%;
  }

  #menu {
    flex-direction: column;
  }

  #menu #dropdown-menu {
    text-align: center;
    visibility: visible;
  }

  ul#menu {
    cursor: pointer;
    position: relative;
  }

  #menu #submenu {
    text-align: center;
    opacity: 0;
    pointer-events: none;
    background-color: #000;
    border-radius: 1rem;
    flex-direction: column;
    width: 100%;
    transition: all .3s;
    display: flex;
    position: absolute;
    top: 100%;
    left: 0;
  }

  #menu #submenu li {
    color: #fff;
    cursor: pointer;
    border-radius: 1rem;
    align-self: center;
    width: 100%;
    padding: 1rem;
    transition: background .2s;
  }

  #menu #dropdown-menu {
    width: 1.2rem;
    padding: .2rem 48%;
    transition: transform .2s;
  }

  #menu:hover #submenu {
    opacity: 1;
    pointer-events: all;
    top: 100%;
  }

  p {
    font-size: 1rem;
  }

  h1 {
    font-size: 3rem;
  }

  main {
    width: 90%;
    margin-left: 5%;
    margin-right: 5%;
  }

  #hero {
    padding: 6.5rem 2rem;
  }

  #about-me, #projects, #connect-with-me {
    padding: 1rem;
  }

  .about-me-hidden {
    transition: all 1s ease-in-out;
    display: none;
  }

  .about-me-hidden.show {
    height: calc-size(auto);
    height: auto;
    display: block;
  }

  #read-more-btn {
    visibility: visible;
    color: #fff;
    background-color: #0000;
    border: none;
    border-radius: 50%;
    width: 2.5rem;
    height: 2.5rem;
    font-size: 1rem;
  }
}
